<template>
    <section>
        <Breadcrumbs :breadcrumbs="breadcrumbs"/>

        <div class="widgets row">
            <InfoWidget
                v-for="widget of widgets"
                :key="widget.title"
                :widget="widget"
            />
        </div>
        
        <Loader v-if="loading"/>
    </section>
</template>

<script>
import Breadcrumbs from '@/components/admin/layout/Breadcrumbs'
import InfoWidget from '@/components/admin/widgets/InfoWidget'
import {mapActions} from 'vuex'

export default {
    components: {Breadcrumbs, InfoWidget},
    metaInfo(){
        return {
            title: this.$title('Главная')
        }
    },
    data: () => ({
        loading: true,
        breadcrumbs: {
            title: 'Главная',
            current: ''
        },
        widgets: [
            {
                title: 'Кол-во заказов',
                count: null,
                color: 'pink-blue',
                pic: 'fas fa-truck'
            },
            {
                title: 'Новых заказов',
                count: null,
                color: 'green',
                pic: 'fas fa-cart-plus'
            },
            {
                title: 'Кол-во товаров',
                count: null,
                color: 'orange-pink',
                pic: 'fas fa-tshirt'
            }
        ]
    }),
    methods: mapActions(['fetchOrdersAmount', 'fetchProductsAmount', 'fetchNewOrdersAmount']),
    mounted(){
        this.fetchOrdersAmount()
        this.fetchProductsAmount()
        this.fetchNewOrdersAmount()
        this.loading = false
        this.widgets[0].count = this.$store.getters.ordersAmount
        this.widgets[1].count = this.$store.getters.newOrdersAmount
        this.widgets[2].count = this.$store.getters.productsAmount
    }
}
</script>